import { graphql } from "gatsby";
import React, { Suspense, useDeferredValue, useEffect, useState } from "react";
import { Main } from "../../components/main";
import { GLTF } from "three-stdlib";
import { Camera, Canvas } from "react-three-fiber";
import { GatsbyImage } from "gatsby-plugin-image";
import { useGLTF, OrbitControls, Stage } from "@react-three/drei";
import { Mesh } from "three";
import styled from "styled-components";
import { BlockTitle } from "../../components/content";

export const Head = ({ data: { contentfulDigitalArt } }) => (
  <title>Kyle Watson - Art - {contentfulDigitalArt.title}</title>
);

const ToggleButton = styled.button`
  position: absolute;
  z-index: 100;
  color: #001b2e;
  font-weight: bold;
  font-size: 1.2rem;
  background-color: #f3b3a6;
  padding: 1rem;
  margin: 1rem;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  transition: ease 0.3s;
  opacity: 0.4;

  :hover {
    opacity: 1;
  }
`;

const Container = styled.div`
  display: flex;
  gap: 1em;

  padding: 3rem 8rem;
  @media only screen and (max-width: 768px) {
    padding: 1rem 3rem;
    align-items: center;
    display: flex;
    flex-direction: column;
    .model {
      max-height: 270px;
      max-width: 270px;
    }
    .model div {
      max-height: 270px;
      max-width: 270px;
    }
    .summary {
      margin-top: 1em;
    }
  }
`;

const ImageFrame = {
  border: "solid 8px white",
  borderRadius: "16px",
  boxShadow: "#00000070 0px 8px 8px",
  background: "#aabdcb",
};

const CanvasRenderer = ({ preRenderedView, url, width, height }) => {
  if (preRenderedView) return null;

  const gltf = useGLTF(url) as GLTF;
  const camera = gltf.cameras[0];
  camera.removeFromParent();

  gltf.scene.traverse((node) => {
    if ((node as Mesh).isMesh) node.castShadow = true;
  });

  return (
    <Canvas
      style={{
        width: width,
        height: height,
        ...ImageFrame,
      }}
      camera={camera as Camera}
    >
      <OrbitControls
        autoRotate
        autoRotateSpeed={0.5}
        minPolarAngle={Math.PI / 8}
        maxPolarAngle={Math.PI / 2}
      />
      <Stage adjustCamera={false}>
        <primitive object={gltf.scene} />
      </Stage>
    </Canvas>
  );
};

export default ({ data: { contentfulDigitalArt } }) => {
  const [preRenderedView, setPreRenderedView] = useState(true);
  const deferredViewToggle = useDeferredValue(preRenderedView);

  return (
    <Main>
      <Container>
        <div className="model">
          <ToggleButton onClick={() => setPreRenderedView(!preRenderedView)}>
            {preRenderedView
              ? "View Realtime Model"
              : "View Pre-rendered Model"}
          </ToggleButton>
          <Suspense fallback={null}>
            <GatsbyImage
              image={contentfulDigitalArt.render.gatsbyImage}
              alt={contentfulDigitalArt.render.title}
              style={{
                display: deferredViewToggle ? "block" : "none",
                width: Math.min(600, contentfulDigitalArt.render.width),
                height: Math.min(600, contentfulDigitalArt.render.height),
                ...ImageFrame,
              }}
            />
            <CanvasRenderer
              preRenderedView={deferredViewToggle}
              url={contentfulDigitalArt.modelFile.url}
              width={Math.min(600, contentfulDigitalArt.render.width)}
              height={Math.min(600, contentfulDigitalArt.render.height)}
            ></CanvasRenderer>
          </Suspense>
        </div>
        <div className="summary">
          <BlockTitle>{contentfulDigitalArt.title}</BlockTitle>
          <div
            dangerouslySetInnerHTML={{
              __html: contentfulDigitalArt.summary.childMarkdownRemark.html,
            }}
          ></div>
        </div>
      </Container>
    </Main>
  );
};

export const data = graphql`
  query pageQuery($id: String!) {
    contentfulDigitalArt(id: { eq: $id }) {
      modelFile {
        url
        title
      }
      slug
      title
      summary {
        childMarkdownRemark {
          html
        }
      }
      render {
        title
        gatsbyImage(layout: CONSTRAINED, height: 600, placeholder: BLURRED)
        width
        height
      }
    }
  }
`;
